@import '../../common/styles/sass/variables';
@import '../../common/styles/sass/mixins';
@import 'src/components/common/components/button/Buton.module';

.project {
  width: 500px;
  display: flex;
  flex-direction: column;
  background-color: $bgSecondColor;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;
  border-radius: 30px;
  @media screen and (max-width: 1127px) {
    margin: 70px auto;
  }

  &:hover {
    box-shadow: 0px 20px 35px burlywood;
    transform: translateY(-5px);
  }
  .imgContainer {
    height: 300px;
    background-color: slategrey;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;


    &::before {
      position: absolute;
      content: '';
      background-color: darkgray;
      opacity: 0.4;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }


    a {
      @extend .btn;
      align-self: center;
      background-color: transparent;
      margin-top: 30px;
      text-decoration: none;
      color: $bgSecondColor;
    }
  }

  .projectInfo {
    padding: 25px 20px;


    .title {
      @include text ($titleFontFamily, 16px, 1.2, 400);
      letter-spacing: 1px;
      margin: 0 0 5px;
      color: $primaryColor;
    }

    .description {
      @include text($textFontFamily, 12px, 1.8, 400);
      font-weight: 400;
      color: $textColor;
    }
  }
}